/**
 *   Name: Bizagi Tablet Column Decorator Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the column decorator class to adjust to tablet devices
 */

// Extends itself
bizagi.rendering.tablet.columns.column.extend("bizagi.rendering.tablet.columns.search", {}, bizagi.rendering.columns.search.prototype );
// Extends itself
bizagi.rendering.tablet.columns.search.extend(
  "bizagi.rendering.tablet.columns.search",
  {},
  {
    renderReadOnly: function(surrogateKey, value) {
      if (value && value.length > 0) {
        value = [].concat(value[0]);
        return this._super(surrogateKey, {
          id: value[0],
          label: value[1],
          additionallabel: value[2] || undefined
        });
      } else {
        return this._super(surrogateKey, value);
      }
    },

    render: function(surrogateKey, value) {
      var self = this;
      if (value && value.length > 0) {
        value = [].concat(value[0]);
        return self._super(surrogateKey, {
          id: value[0],
          label: value[1],
          additionallabel: value[2] || undefined
        });
      } else {
        return self._super(surrogateKey, value);
      }
    },

    /*
     *   Post process the element after it has been rendered
     */
    postRender: function(surrogateKey, cell) {
      // Call base
      this._super(surrogateKey, cell);

      var decorated = this.getDecorated(surrogateKey);
      decorated.refreshControl = this.refreshControl;
    },

    refreshControl: function() {
      this.grid.refreshCell({
        key: this.surrogateKey,
        column: this.properties.xpath,
        xpathContext: this.getFormContainer().properties.xpathContext
      });
    }
  }
);