/**
 *   Name: Bizagi Tablet Render Money Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the money render class to adjust to tablet devices
 */

// Extends from render tablet base
bizagi.rendering.tablet.render.extend("bizagi.rendering.tablet.money", bizagi.rendering.number.prototype);
// Extends itself
bizagi.rendering.tablet.money.extend("bizagi.rendering.tablet.money", bizagi.rendering.money.prototype);
// Extends itself
bizagi.rendering.tablet.money.extend("bizagi.rendering.tablet.money", {}, {

    /**
     * Method to customize each render after processed in read-only mode
     * @returns {*|jQuery}
     */
    renderReadOnly: function () {
        var self = this;
        var template = self.renderFactory.getTemplate("number");

        return $.fasttmpl(template, {
            value: self.getDisplayValue(),
            editable: self.properties.editable,
            type: "text",
            isDesign: self.getMode() !== "execution"
        });
    },

    /**
     * Sets the value in the rendered control
     * @param {} value
     * @returns {}
     */
    setDisplayValue: function (value) {
        var self = this;

        // Call base
        self.setValue(value, false);

        // Set value in input
        if (value !== undefined && value !== null && self.properties.editable) {
            self.numericInput.val(value);

            // Formats the input
            self.executeFormatCurrencyPlugin();
        }
    },

    /**
     * Context to apply styles
     * @param context
     * @returns {jQuery|HTMLElement}
     */
    getElementsToStylize: function ($context) {
        return $("input", $context); 
    }
});
