/**
 *   Name: Bizagi Tablet Render Grid Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the grid render class to adjust to tablet devices
 */

// Extends from render tablet base
bizagi.rendering.tablet.render.extend("bizagi.rendering.tablet.grid", bizagi.rendering.grid.prototype);
// Extends itself
bizagi.rendering.tablet.grid.extend("bizagi.rendering.tablet.grid", {
    GRID_PAGE_SIZE: 10
}, {
    /**
     * Constructor
     * @param params
     */
    init: function (params) {
        var self = this;
        var data = params.data;

        // Create a single instance for each cell
        data.properties.singleInstance = false;

        // Call base
        this._super(params);

        // Create a data-structure to keep track of each cell properties
        this.cellMetadata = {};

        // Check if offline forms is supported
        this.isOfflineForm = bizagi.util.isOfflineForm({context: self});

        // Fill default properties
        var properties = this.properties;
        var form = self.getFormContainer();

        // If not helptext configured, Just apply display-type: vertical, align it to the left
        if (self.properties.helpText && self.properties.helpText != null && self.properties.helpText != "") {
            properties.displayType = "value";
        } else {
            properties.displayType = "vertical";
        }

        properties.labelAlign = "left";

        if (properties.groupBy || properties.groupSummary) {
            // Add warning
            form.addWarning(self.getResource("render-tablet-warning-grid"));
        }
    },

    /* method overrides
    *  from base or virtual
    */

    /**
     * Update or init the element data
     * @param data
     */
    initializeData: function (data) {

        // Create a single instance for each cell
        data.properties.singleInstance = false;

        // Call base
        this._super(data);
    },

    /**
     * Updates manually the affected cell
     */
    updateAffectedCellManually: function () {
    },

    /**
     * Get the row index using the key as a reference
     */
    getRowIndexByKey: function () {
    },

    /**
     * Template method to implement in each device to customize each render after processed
     */
    postRender: function () {
        var self = this;
        var properties = self.properties;
        var control = self.getControl();

        self.initialLoadDone = false;

        // Call base 
        self._super();

        // Find component holder
        self.grid = $(">div", control);

        // Hide common label
        self.getLabel().hide();

        // Grid plugin
        self.applyGridPlugin();

        // Set initial data
        if (properties.data) {

            self.updateData(properties.data);

            // Trigger change in order to start up the actions when the controls is ready
            $.when(self.ready())
                .done(function () {
                    self.triggerRenderChange({changed: false});
                });
        }

        // Execute pending actions
        self.initialLoadDone = true;

        for (var i in self.pendingActions) {
            if (self.pendingActions.hasOwnProperty(i)) {
                self.pendingActions[i].method.apply(self, self.pendingActions[i].arguments);
            }
        }
    },

    /**
     * Method to render non editable values
     * @returns {}
     */
    applyGridPlugin: function () {
        var self = this;
        var properties = self.properties;
        var mode = self.getMode();
        var grid = self.grid;

        // Build column metadata
        var columns = self.buildColumnModel();

        var columnsTotalizers = (columns || []).filter(function (item) {
            return (item.summarizeBy);
        });

        // Apply grid plugin
        grid.bizagi_grid_tablet({
            columns: columns,
            subType: properties.subType || grid,
            title: properties.displayName,
            isOfflineForm: self.isOfflineForm || false,
            mode: mode,
            template: {
                grid: self.renderFactory.getTemplate("bizagi.grid.grid"),
                waiting: self.renderFactory.getTemplate("bizagi.grid.waiting"),
                table: self.renderFactory.getTemplate("bizagi.grid.table"),
                emptyTable: self.renderFactory.getTemplate("bizagi.grid.table.empty"),
                column: self.renderFactory.getTemplate("bizagi.grid.column"),
                specialColumn: self.renderFactory.getTemplate("bizagi.grid.column.special"),
                row: self.renderFactory.getTemplate("bizagi.grid.row"),
                rowButtons: self.renderFactory.getTemplate("bizagi.grid.row.buttons"),
                cell: self.renderFactory.getTemplate("bizagi.grid.cell"),
                specialCell: self.renderFactory.getTemplate("bizagi.grid.cell.special"),
                pager: self.renderFactory.getTemplate("bizagi.grid.pager"),
                buttons: self.renderFactory.getTemplate("bizagi.grid.buttons"),
                dynamicPager: self.renderFactory.getTemplate("bizagi.grid.dynamicpager"),
                totalizer: self.renderFactory.getTemplate("bizagi.grid.totalizer")
            },
            actions: {
                add: properties.allowAdd, //|| properties.inlineAdd,
                edit: (properties.allowEdit && properties.withEditForm && properties.data != null) || (properties.subType === "adhocGrid" && properties.allowEdit),
                details: properties.allowDetail,
                remove: properties.allowDelete,
                inlineAdd: false, //properties.inlineAdd,
                allowMore: properties.allowMore,
                enableXlsExport: false,
                enablePdfExport: false,
                hasTotalizer: columnsTotalizers && columnsTotalizers.length > 0 && properties.data.rows.length > 0
            },
            pageRequested: function (ui) {
                if (!self.isExecutionMode()) return;
                if ((self.changes && Object.keys(self.changes).length >= 1)|| (self.exclusiveChanges && Object.keys(self.exclusiveChanges).length >= 1)){
                    $.when(self.getFormContainer().saveForm()).done(function () {
                        self.fetchData(ui.page, ui.sortBy, ui.sortType);
                    });
                } else {
                    self.fetchData(ui.page, ui.sortBy, ui.sortType);
                }
                
            },
            sortRequested: function (ui) {
                if (!self.isExecutionMode()) return;
                self.fetchData(ui.page, ui.sortBy, ui.sortType);
            },
            drawCell: function (ui) {
                return self.drawCell(ui.column, ui.key, ui.value, ui.isNewRow);
            },
            cellReady: function (ui) {
                return self.onCellReady(ui.column, ui.key, ui.cell, ui.isNewRow);
            },
            rowSelected: function (ui) {
                if (!self.isExecutionMode()) return;
                self.triggerHandler("rowSelected", {id: ui.key, data: ui});
            },
            addRow: function (ui) {
                if (!self.isExecutionMode()) return;
                if (self.properties.subType === "adhocGrid" || self.properties.inlineAdd) {
                    self.addVirtualRow();
                } else {
                    self.addRow();
                }
            },
            editRow: function (ui) {
                if (!self.isExecutionMode()) return;
                if (self.properties.subType === "adhocGrid") {
                    self.editVirtualRow(ui.key);
                } else {
                    self.editRow(ui.key);
                }
            },
            deleteRow: function (ui) {
                if (!self.isExecutionMode()) return;
                self.deleteRow(ui.key);
            },
            moreRows: function (ui) {
                if (!self.isExecutionMode()) return;
                self.moreRows(ui);
            },
            showFormDetails: function (ui) {
                self.showDetailForm(ui.key);
            },
            showMore: function (ui) {
                if (!self.isExecutionMode()) return;
                self.showMore(ui.key);
            },
            totalData: function (ui) {
                if (!self.isExecutionMode()) return;
                self.onTotalData();
            }
        });

    },

    /**
     * Method to render non editable values
     */
    postRenderReadOnly: function () {
        // Do the same as the post-render            
        this.postRender();
    },

    /**
     *   Creates the column model required to initialize the grid plugin
     */
    buildColumnModel: function () {
        var self = this;
        var columns = self.columns;

        // Create id column first
        var keyColumn = {
            name: "id",
            index: 0,
            label: "id",
            hidden: true,
            align: "center",
            required: false,
            key: true
        };

        // Add to column model
        var columnModel = [];
        columnModel.push(keyColumn);

        $.each(columns, function (index, column) {

            // Set grid column
            column.columnIndex = index + 1;
            var columnLabel = !bizagi.util.isEmpty(column.properties.displayName) ? column.properties.displayName : " ";
            var gridColumn = {
                name: column.properties.xpath,
                index: index + 1,
                label: columnLabel,
                required: column.properties.required || false,
                key: false,
                hidden: (!column.properties.visible || column.properties.type == "columnHidden" || column.properties.columnVisible == false),
                summarizeBy: (column.properties.totalize && column.properties.totalize.operation ? column.properties.totalize.operation : undefined),
                bizAgiProperties: column.properties
            };

            columnModel.push(gridColumn);
        });

        return columnModel;
    },

    /**
     * Makes the grid to refresh
     */
    refresh: function () {
        this.fetchData();
    },

    /*
    *   Holds the execution until the grid data is ready after a load operation
    */
    dataReady: function () {
        return this.dataReadyDeferred != null ? this.dataReadyDeferred.promise() : null;
    },

    /*
    * Update data from local changes
    */
    loadChanges: function (rows) {
        var self = this;

        // Get data from the rows
        for (var rowIndex = 0; rowIndex < rows.length; rowIndex++) {
            var rowChanges = self.changes[rows[rowIndex][0]];
            if (self.exclusiveChanges) {
                rowChanges = $.extend(rowChanges || {}, self.getExclusiveChangeByRow(rows[rowIndex][0]));
            }
            // Search for changes
            if (rowChanges !== undefined) {
                // Update all rowChanges in data
                for (var xpath in rowChanges) {
                    if (rowChanges.hasOwnProperty(xpath)) {
                        var columnData = self.getColumn(xpath);
                        rows[rowIndex][columnData.columnIndex] = self.mapChangeValue(columnData.properties.type, rowChanges[xpath], columnData.comboData);
                    }
                }
            }
        }
    },

   
    /**
     * Mapper to data changes
     * @param type
     * @param change
     * @param data
     * @returns {(*|number)[][]}
     */
    mapChangeValue: function (type, change, data) {
        data = data || [];
        var value = change;

        if (change !== null && typeof change !== "undefined") {
            if (type === "columnCombo" || type === "columnRadio") {
                if (!bizagi.util.isArray(change)) {
                    var item = data.find(function (el) {
                        return el.id == change;
                    });

                    if (item) {
                        value = [[item.id, item.value]];
                    }
                }
            }
        }

        return value;
    },

    /*
    *   Method to set data and update the grid
    */
    updateData: function (data) {
        var self = this;
        var grid = self.grid;
        var columns = self.columns;

        // Set value in control
        if (data) {
            self.properties.data = data;

            self.setCellOverrides(data, columns);
            self.triggerGridPluginHandler("setData", data);
        }
    },

    /*
    *   Method to fetch data from the server and then update the data
    */
    fetchData: function (page, sortBy, sortType) {
        var self = this;
        var properties = self.properties;

        self.dataReadyDeferred = new $.Deferred();

        // Define defaults
        page = page || (properties.page || 1);
        sortBy = sortBy || (properties.sortBy || "");
        sortType = sortType || (properties.sortOrder || "asc");

        // Update control properties
        properties.page = page;
        properties.sortBy = sortBy;
        properties.sortType = sortType;
        properties.sort = sortBy + " " + sortType;

        // Start loading to avoid multiple calls to the server
        self.startLoading();

        $.when(self.getRemoteData())
            .then(function (data) {
                if (data) {
                    if (data.deviceMaxRecordsExceeded) {
                        bizagi.showMessageBox(bizagi.localization.getResource("render-search-maximum-records-allowed"));
                    } else {
                        if (self.isRTL()) {
                            data.rows = self.changeOrderData(data.rows);
                        }

                        self.loadChanges(data.rows);
                        self.updateData(data);

                        self.triggerRenderChange({changed: false});

                        self.dataReadyDeferred.resolve();
                    }
                }
            }).always(function () {
            self.endLoading();
        });
    },


    /*
    *   Add the render data to the given collection in order to send data to the server
    */
    collectData: function (renderValues) {
        var self = this;
        var grid = self.grid;

        // Save pending inline add rows
        if (self.grid && (self.properties.inlineAdd || self.properties.inlineEdit)) {
            var newKeys = grid.bizagi_grid_tablet.hasOwnProperty("getNewRowKeys") ? grid.bizagi_grid_tablet("getNewRowKeys") : [];

            // Save new rows then save the whole screen
            if (newKeys.length > 0 && !self.properties.alreadySaved && self.isValid()) {
                var saveDeferred = self.saveInlineAddedRows(newKeys, true);

                // Add to deferred collections in order to sync when saving
                renderValues.deferreds = renderValues.deferreds || [];
                renderValues.deferreds.push(saveDeferred);
            }

            // Remove adding keys from current changes
            for (var i = 0; i < newKeys.length; i++) {
                if (self.changes[newKeys[i]]) {
                    delete self.changes[newKeys[i]];
                }
            }
        }

        // Call base method
        self._super(renderValues);
    },

    /*
    *   Customizes the content drawing inside the cell
    */
    drawCell: function (column, key, value, isNewRow) {
        var self = this;
        if (column.key) return value;

        self.newRecords = self.newRecords || {};
        // Get render column
        var columnIndex = column.index - 1;
        var renderColumn = self.columns[columnIndex];

        // Get cell properties
        if (!self.cellMetadata[key]) self.cellMetadata[key] = {};
        if (!self.cellMetadata[key][columnIndex]) self.cellMetadata[key][columnIndex] = {};

        var properties = self.cellMetadata[key][columnIndex].properties || renderColumn.originalProperties;
        properties.editable = bizagi.util.parseBoolean(properties.editable) || false;
        var visible = properties.visible !== undefined ? properties.visible : true;
        var editable = properties.editable !== undefined ? properties.editable : true;


        // Show editable cell when we are performing inline add operation
        if (isNewRow) editable = true;

        renderColumn.properties.displayType = 'value';
        renderColumn.setSurrogateKey(key);

        if (key === "summary") {
            editable = false;
        }

        renderColumn.properties.labelAlign = "default";
        if (renderColumn.properties.columntitlealign) {
            renderColumn.properties.labelAlign = renderColumn.properties.columntitlealign;
        }

        renderColumn.properties.valueAlign = "default";
        if (renderColumn.properties.columnAlign) {
            renderColumn.properties.valueAlign = renderColumn.properties.columnAlign;
        }

        if (properties.visible) {
            if (editable) {
                return self.drawEditableCell(renderColumn, properties, key, value, isNewRow);
            } else {
                return self.drawReadonlyCell(renderColumn, properties, key, value);
            }
        } else {
            return "";
        }
    },

    /*
    *   Executes when the cell is ready and inserted into the DOM
    */
    onCellReady: function (column, key, cell, isNewRow) {
        var self = this;

        // Get render column
        var columnIndex = column.index - 1;
        var renderColumn = self.columns[columnIndex];
        var properties = ((self.cellMetadata[key][columnIndex] != undefined) ? self.cellMetadata[key][columnIndex].properties : undefined) || renderColumn.originalProperties;
        var visible = properties.visible !== undefined ? properties.visible : true;
        var editable = properties.editable !== undefined ? properties.editable : true;

        // Show editable cell when we are performing inline add operation
        if (isNewRow) editable = true;
        else editable = false;
        if (visible) {
            if (editable) {
                // Set editable
                var originalEditable = renderColumn.properties.editable;
                renderColumn.properties.editable = editable;

                if (renderColumn.properties.submitOnChange) {
                    var control = renderColumn.getDecorated(key);
                    if (control) {
                        control.properties.submitOnChangexpathContext = self.properties.xpath + "[]";
                    }
                }


                // Execute cell post render
                renderColumn.postRender(key, cell);

                // Restore editable
                renderColumn.properties.editable = originalEditable;
            } else {
                renderColumn.postRenderReadOnly(key, cell);
            }
        }
    },

    /**
     *  Adds a row to the grid
     */
    addRow: function () {
        var self = this;
        var properties = self.properties;
        var form = self.getFormContainer();
        var formParams = self.getFormParams();

        // Start loading
        self.startLoading();

        // Send add request
        $.when(self.submitAddRequest())
            .done(function (newid) {

                // Show a slide with new form after that
                var slideView = new bizagi.rendering.tablet.slide.view(self.dataService, self.renderFactory, {
                    title: self.properties.displayName || "",
                    container: form.container,
                    formParams: formParams,
                    navigation: formParams.navigation,
                    showSaveButton: true,
                    onSave: function (data) {                 
                        self.addExclusiveChange(newid, data);
                        return self.submitSaveRequest(newid, data);
                    }
                });

                // End Loading
                self.endLoading();

                var recordXpath = properties.xpath + "[id=" + newid + "]";

                slideView.render({
                    idRender: properties.id,
                    recordXPath: recordXpath,
                    xpathContext: properties.xpathContext,
                    idPageCache: properties.idPageCache,
                    requestedForm: "addForm",
                    url: properties.addPage,
                    formParams: formParams,
                    navigation: formParams.navigation
                }).done(function () {
                    if (!properties.submitOnChange) {
                        // Reload grid
                        self.fetchData();
                    } else {
                        // Submits the entire form
                        self.submitOnChange();
                    }

                }).fail(function () {
                    self.submitRollbackRequest();
                }).always(function () {
                    self.endLoading();
                })
            }).always(function () {
            self.endLoading();
        });

    },

    addVirtualRow: function () {
        var self = this;
        var properties = self.properties;
        var form = self.getFormContainer();
        var formParams = self.getFormParams();

        // Create mock row
        var currentRow = ["mock_id"];
        self.columns.forEach(function () {
            currentRow.push(null);
        });

        // Render grid view
        var slideView = new bizagi.rendering.tablet.slide.adhocForm(self.renderFactory, {
            title: self.properties.displayName || "",
            allowEdition: true,
            navigation: formParams.navigation
        });

        slideView.render({
            row: currentRow,
            columns: self.columns,
            pageCacheId: properties.idPageCache,
            xpath: properties.xpath,
            xpathContext: properties.xpathContext,
            sessionId: form.properties.sessionId,
            actions: form.actions
        }).done(function (data) {
            $.when(self.saveAddedRow(data)).done(function () {
                if (!properties.submitOnChange) {
                    // Reload grid
                    self.fetchData();
                } else {
                    // Submits the entire form
                    self.submitOnChange();
                }
            });
        });
    },

    /*
    *   Prepares the add action
    */
    saveAddedRow: function (data) {
        var self = this;
        var properties = self.properties;

        return self.dataService.multiaction().addGridRecordData({
            url: properties.addUrl,
            idRender: properties.id,
            xpath: properties.xpath,
            xpathContext: properties.xpathContext,
            contexttype: properties.contexttype,
            idPageCache: properties.idPageCache,
            submitData: data
        });
    },

    /**
     *  Edits a row in the grid
     */
    editRow: function (id) {

        if (id == 0) {
            bizagi.showMessageBox(bizagi.localization.getResource("render-grid-message-no-selected-row"), bizagi.localization.getResource("render-grid-header-no-selected-row"));
            return;
        }

        var self = this;
        var properties = self.properties;

        // Use tablet inline edit when there are no edit form
        if (properties.inlineEdit == true && properties.allowEdit == false) {
            self.editInline(id);
            return;
        }

        if (self.cleanActionChanges && self.cleanActionChanges[id] !== undefined) {

            self.changes = self.cleanActionChanges;
            var data = $.extend(self.changes[id], {});
            data.idPageCache = properties.idPageCache;

            $(".ui-tooltip").remove();

            $.when(self.submitSaveRequest(id, data)).done(function () {
                // Set loading status
                self.startLoading();

                if (!properties.submitOnChange) {
                    // Reload grid
                    self.endLoading();

                    self.fetchData();
                } else {
                    // Submits the entire form
                    self.submitForGridChange().always(function () {
                        self.endLoading();

                        // Send edit request
                        self.executeSubmitEditRequest(id, function(data){
                            self.addExclusiveChange(id, data);
                        });
                    });
                }
            });
        } else {
            self.executeSubmitEditRequest(id, function(data){
                self.addExclusiveChange(id, data);
            });
        }
    },
    editVirtualRow: function (id) {
        var self = this;
        var data = self.properties.data;
        var properties = self.properties;
        var form = self.getFormContainer();
        var formParams = self.getFormParams();
        // Search row
        var currentRow = [];
        for (var i = 0, length = data.rows.length; i < length; i++) {
            if (data.rows[i][0] == id) {
                currentRow = data.rows[i];
                break;
            }
        }
        // Render grid view
        var slideView = new bizagi.rendering.tablet.slide.adhocForm(self.renderFactory, {
            title: self.properties.displayName || "",
            allowEdition: true,
            navigation: formParams.navigation
        });
        slideView.render({
            row: currentRow,
            columns: self.columns,
            pageCacheId: properties.idPageCache,
            xpath: properties.xpath,
            xpathContext: properties.xpathContext,
            sessionId: form.properties.sessionId,
            actions: form.actions
        }).done(function (formData) {
            // This code is executed when the user press save on the slide
            // Update internal data
            for (xpath in formData) {
                self.collectGridChange({id: id, xpath: xpath, value: formData[xpath], trigger: false});
            }
            // Prepare grid data
            var dataToSend = {};
            self.collectData(dataToSend);
            // Send the data
            self.dataService.submitData({
                action: "SUBMITONCHANGE",
                data: dataToSend,
                idRender: properties.id,
                xpath: properties.xpath,
                xpathContext: properties.xpathContext,
                idPageCache: properties.idPageCache
            }).done(function () {
                // After successfully response refresh the grid
                self.fetchData();
            });
        });
    },

    executeSubmitEditRequest: function (id, callback) {
        var self = this;
        var properties = self.properties;
        var form = self.getFormContainer();
        var formParams = self.getFormParams();


        var slideView = new bizagi.rendering.tablet.slide.view(self.dataService, self.renderFactory, {
            title: self.properties.displayName || "",
            container: form.container,
            formParams: formParams,
            navigation: formParams.navigation,
            showSaveButton: true,
            onSave: function (data) {
                if(callback){
                    callback(data);
                }                
                return self.submitSaveRequest(id, data);
            }
        });

        self.startLoading();

        $.when(self.submitEditRequest(id))
            .done(function () {

                // Show dialog with new form after that
                var recordXpath = properties.xpath + "[id=" + id + "]";

                slideView.render({
                    idRender: properties.id,
                    recordXPath: recordXpath,
                    xpathContext: properties.xpathContext,
                    idPageCache: properties.idPageCache,
                    requestedForm: "editForm",
                    url: properties.editPage,
                    formParams: formParams,
                    navigation: formParams.navigation
                }).done(function () {
                    if (!properties.submitOnChange) {
                        // Reload grid
                        self.fetchData();

                    } else {
                        // Submits the entire form
                        self.submitOnChange();
                    }
                }).fail(function () {
                    self.startLoading();
                    $.when(self.submitRollbackRequest()).always(function () {
                        self.endLoading();
                    });
                });
            }).always(function () {
            self.endLoading();
        });
    },

    showDetailForm: function (id) {
        var self = this;
        var properties = self.properties;
        var form = self.getFormContainer();
        var formParams = self.getFormParams();

        var slideView = new bizagi.rendering.tablet.slide.view(self.dataService, self.renderFactory, {
            title: self.properties.displayName || "",
            container: form.container,
            formParams: formParams,
            navigation: formParams.navigation,
            showSaveButton: false
        });

        // Show dialog with new form after that
        var recordXpath = properties.xpath + "[id=" + id + "]";

        slideView.render({
            idRender: properties.id,
            recordXPath: recordXpath,
            xpathContext: properties.xpathContext,
            idPageCache: properties.idPageCache,
            requestedForm: "detailForm",
            url: properties.editPage,
            formParams: formParams,
            navigation: formParams.navigation
        });
    },
    /**
     *  Deletes  a row to the grid
     */
    deleteRow: function (id) {
        if (id === 0) {
            bizagi.showMessageBox(
                bizagi.localization.getResource("render-grid-message-no-selected-row"),
                bizagi.localization.getResource("render-grid-header-no-selected-row")
            );
            return;
        }

        var self = this;

        bizagi.showConfirmationBox(this.getResource("render-grid-delete-confirmation"))
            .done(function () {
                // Do a grid record deletion
                $.when(self.submitDeleteRequest(id))
                    .done(function (data) {
                        // Reload grid
                        self.fetchData();

                        if (data.type === "success") {
                            // Trigger the event
                            //'key' must be included to trigger a single call to actions and validations
                            //line 310 bizagi.command.controllers.action
                            self.triggerRenderChange({rowDeleted: true, changed: false, key: id});
                        }
                    });
            });
    },

    moreRows: function (ui) {
        var self = this;
        var properties = self.properties;

        if (typeof properties.originalrowsPerPage === "undefined") {
            properties.originalrowsPerPage = properties.rowsPerPage;
        }

        properties.rowsPerPage += properties.originalrowsPerPage;

        var dataToSend = {};
        self.collectData(dataToSend);

        self.startLoading();

        if ($.isEmptyObject(dataToSend)) {
            self.moreRowsCommon(ui);
            return;
        }

        // Send the data
        self.dataService.submitData({
            action: "SUBMITONCHANGE",
            data: dataToSend,
            idRender: properties.id,
            xpath: properties.xpath,
            xpathContext: properties.xpathContext,
            idPageCache: properties.idPageCache
        }).done(function () {

            self.endLoading();

            self.moreRowsCommon(ui);
        });

    },

    moreRowsCommon: function (ui) {
        var self = this;
        var properties = self.properties;

        self.fetchData(ui.page, ui.sortBy, ui.sortType);

        $.when(self.dataReadyDeferred).done(function () {

            if (properties.records != 0)
                if (properties.records + 1 <= properties.rowsPerPage) {
                    $(".ui-bizagi-grid-dynamic-pager", self.getControl()).hide();
                }

        });
    },

    /**
     *  Shows more info about a grid row
     */
    showMore: function (id) {
        var self = this;
        var data = self.properties.data;
        var properties = self.properties;
        var form = self.getFormContainer();
        var formParams = self.getFormParams();

        // Search row
        var currentRow = [];
        for (var i = 0, length = data.rows.length; i < length; i++) {
            if (data.rows[i][0] == id) {
                currentRow = data.rows[i];
                break;
            }
        }

        // Render grid view
        var slideView = new bizagi.rendering.tablet.slide.grid(self.dataService, self.renderFactory, {
            title: self.properties.displayName || "",
            container: form.container,
            allowEdition: false,
            formParams: formParams,
            navigation: formParams.navigation
        });

        self.startLoading();

        slideView.render({
            row: currentRow,
            columns: self.columns,
            pageCacheId: properties.idPageCache,
            xpath: properties.xpath,
            xpathContext: properties.xpathContext,
            sessionId: form.properties.sessionId,
            actions: form.actions,
            formParams: formParams,
            navigation: formParams.navigation
        }).done(function () {
            self.endLoading();
        });
    },

    /**
     *  Edits the entire row (inline) in another slide
     */
    editInline: function (id) {
        var self = this;
        var data = self.properties.data;
        var properties = self.properties;
        var form = self.getFormContainer();
        var formParams = self.getFormParams();

        // Search row
        var currentRow = [];
        for (var i = 0, length = data.rows.length; i < length; i++) {
            if (data.rows[i][0] == id) {
                currentRow = data.rows[i];
                break;
            }
        }

        // Render grid view
        var slideView = new bizagi.rendering.tablet.slide.grid(self.dataService, self.renderFactory, {
            title: self.properties.displayName || "",
            container: form.container,
            formParams: formParams,
            navigation: formParams.navigation,
            allowEdition: true
        });

        // Render the editable view
        $.when(slideView.render({
            row: currentRow,
            columns: self.columns,
            pageCacheId: properties.idPageCache,
            xpath: properties.xpath,
            xpathContext: properties.xpathContext,
            sessionId: form.properties.sessionId,
            actions: form.actions,
            validations: form.validations,
            formParams: formParams,
            navigation: formParams.navigation
        })).done(function (data) {
            // This code is executed when the user press save on the slide

            // Update internal data
            for (xpath in data) {
                self.collectGridChange({id: id, xpath: xpath, value: data[xpath], trigger: false});
            }

            // Prepare grid data
            var dataToSend = {};
            self.collectData(dataToSend);

            // Send the data
            self.dataService.submitData({
                action: "SUBMITONCHANGE",
                data: dataToSend,
                idRender: properties.id,
                xpath: properties.xpath,
                xpathContext: properties.xpathContext,
                idPageCache: properties.idPageCache
            }).done(function () {
                // After successfully response refresh the grid
                self.fetchData();
            });
        });
    },

    /*
    *   Changes the background for a cell
    */
    changeCellBackgroundColor: function (key, xpath, argument) {
        var self = this;

        if (self.initialLoadDone) {
            for (var j = 0; j < self.columns.length; j++) {
                var isVisible = self.getCellOverride(key, j).visible;
                var columnProperties = self.columns[j].properties;

                if (self.isValidXPathId(columnProperties.xpath, columnProperties.id, xpath) && isVisible) {
                    var cell = self.grid.bizagi_grid_tablet('getCell', key, xpath, j);

                    if (self.properties.editable) {
                        var cellInput = cell.find(".bz-input-icon");
                        if(columnProperties.type == "columnRadio" || columnProperties.type == "columnBoolean") {
                            cell = cell.find(".bz-radio-list");
                        } else if(cellInput.length != 0) {
                            cell = cellInput;
                        } else {
                            cell = cell.find("input");
                        }
                    }

                    cell.css("background-color", argument);
                    break;
                }
            }

        } else {
            if (self.pendingActions) {
                self.pendingActions.push({
                    method: self.changeCellBackgroundColor,
                    arguments: arguments
                });
            }
        }
    },

    /*
    *   Changes the background for a cell
    */
    changeCellColor: function (key, xpath, argument) {
        var self = this;

        if (self.initialLoadDone) {
            for (var j = 0; j < self.columns.length; j++) {
                var columnProperties = self.columns[j].properties;

                if (self.isValidXPathId(columnProperties.xpath, columnProperties.id, xpath)) {
                    var cell = self.grid.bizagi_grid_tablet('getCell', key, xpath, j);

                    if (self.properties.editable) {
                        cell = cell.find("input");
                    }

                    cell.css("color", argument);
                    break;
                }
            }
        } else {
            if (self.pendingActions) {
                self.pendingActions.push({
                    method: self.changeCellColor,
                    arguments: arguments
                });
            }
        }
    },

    /*
    *   Changes the visibility for a cell
    */
    changeCellVisibility: function (key, xpath, argument) {
        var self = this;

        if (self.initialLoadDone) {

            var index = (self.columns || [])
                .findIndex(function (item) {
                    return self.isValidXPathId(item.properties.xpath, item.properties.id, xpath);
                }) || 0;

            var cell = self.grid.bizagi_grid_tablet('getCell', key, xpath, index);
            if (cell) {
                // Change visibility
                if (argument == true) {
                    cell.css("visibility", "visible");
                    cell.css("display", "");

                    self.element && self.element.show();
                } else {
                    cell.css("visibility", "hidden");
                }
            }

            self.cellOverrides[key] = self.cellOverrides[key] || {};
            self.getCellOverride(key, index).visible = argument;

        } else {
            if (self.pendingActions) {
                self.pendingActions.push({
                    method: self.changeCellVisibility,
                    arguments: arguments
                });
            }
        }
    },

    /*
    *  Changes the required for cell
    */
    changeCellValue: function (key, xpath, argument) {
        var self = this;

        if (self.initialLoadDone) {
            var control = self.getControlCell(key, xpath);
            if (control) {
                control.setDisplayValue(argument);

                var renderColumn = control.column;

                if (renderColumn) {
                    var _value = renderColumn.getValue(key);
                    var compositeValue = renderColumn.getCompositeValue(key);

                    _value = self.getCellValueFormatted(renderColumn, _value);

                    control.setDisplayValue(argument);
                    control.triggerHandler("renderchange", {render: self, changed: true});

                    self.collectGridChange({
                        id: key,
                        xpath: xpath,
                        value: _value,
                        compositeValue: compositeValue,
                        columnIndex: renderColumn,
                        trigger: false
                    });
                }
            }
        } else {
            if (self.pendingActions) {
                self.pendingActions.push({
                    method: self.setDisplayValue,
                    arguments: arguments
                });
            }
        }
    },

    changeCellRequired: function (key, xpath, argument) {
        var self = this;

        if (self.initialLoadDone) {
            var control = self.getControlCell(key, xpath);
            if (control) {
                control.changeCellRequired(argument);
                for (var j = 0; j < self.columns.length; j++) {
                    if (
                        self.columns[j].properties.xpath == xpath ||
                        self.columns[j].properties.id == xpath ||
                        self.columns[j].properties.xpath + "." + self.columns[j].properties.id  == xpath
                    ){
                        break;
                    }                    
                }
                self.cellOverrides[key] = self.cellOverrides[key] || {};
                self.getCellOverride(key, j).required = argument;
            }
        } else {
            self.pendingActions.push({method: self.changeCellRequired, arguments: arguments});
        }
    },

    /*
    *   Sets an error on the cell
    */
    setError: function (key, xpath) {
        var self = this;

        if (self.initialLoadDone) {
            for (var j = 0; j < self.columns.length; j++) {
                var isVisible = self.getCellOverride(key, j).visible;
                var columnProperties = self.columns[j].properties;

                if (self.isValidXPathId(columnProperties.xpath, columnProperties.id, xpath) && isVisible) {
                    var cell = self.grid.bizagi_grid_tablet('getCell', key, xpath, j);
                    cell.parent().addClass("ui-bizagi-render-grid-cell-error");
                    break;
                }
            }

        } else {
            if (self.pendingActions) {
                self.pendingActions.push({
                    method: self.setError,
                    arguments: arguments
                });
            }
        }
    },

    /*
    *   Returns a promise that will resolve when the element is ready
    */
    ready: function () {
        var self = this;
        var parentPromise = self._super();
        var inlineAddPromise = self.inlineAddDeferred ? self.inlineAddDeferred.promise() : null;
        return $.when(parentPromise, inlineAddPromise);
    },

    /*
    *   Method to start loading for ajax data and update the ui
    */
    startLoading: function (bUseTimeout) {
        var self = this;

        bizagi.loading.start();

        self._super(bUseTimeout);
    },

    /*
    *   Method to remove the loading message
    */
    endLoading: function () {
        bizagi.loading.stop();
        this._super();
    },

    cleanCellData: function (key, xpath) {
        var self = this;

        if (self.initialLoadDone) {
            var control = self.getControlCell(key, xpath);
            if (control) {
                control.cleanData();
                for (var j = 0; j < self.columns.length; j++) {
                    var columnProperties = self.columns[j].properties;

                    if (self.isValidXPathId(columnProperties.xpath, columnProperties.id, xpath)) {
                        break;
                    }
                }

                var rows = self.properties.data.rows;
                var i = 0;
                var rowLength = rows.length;

                for (; i < rowLength; i++) {
                    if (Number(key) === rows[i][0]) {
                        rows[i][j + 1] = [["", ""]];
                        break;
                    }
                }
                self.cellOverrides[key] = self.cellOverrides[key] || {};
                self.getCellOverride(key, j).value = null;

                if (!self.cleanActionChanges) {
                    self.cleanActionChanges = {};
                }
                if (!self.cleanActionChanges[key]) {
                    self.cleanActionChanges[key] = {};
                }
                self.cleanActionChanges[key][xpath] = "";
            }
        } else {
            self.pendingActions.push({
                method: self.cleanData,
                arguments: ""
            });
        }
    },

    onTotalData: function (ui) {
        var self = this;
        var properties = self.properties;
        var data = properties.data || {};
        var rows = data.rows;
        var rowsLength = rows.length;
        var showTotal = [];
        var columnsModel = self.buildColumnModel();
        var result;

        bizagi.util.tablet.startLoading();

        columnsModel.forEach(function callback(column, columnIndex) {
            if (column.bizAgiProperties) {
                operationState = column.bizAgiProperties.totalize.operation;

                switch (operationState) {
                    case "sum":
                        result = rows.sum(function (row) {
                            var return_value = row[columnIndex] !== null ? row[columnIndex] : column.bizAgiProperties.defaultvalue;
                            return Number(return_value)
                        }, columnIndex);
                        break;

                    case "count":
                        result = rows.count(function (row) {
                            //Checks if there is a DefaultValue, so assign it to the returnedvalu in case it doesn't have value
                            var return_value = row[columnIndex] !== null ? row[columnIndex] : column.bizAgiProperties.defaultvalue;

                            if (!bizagi.util.isArray(return_value)) {
                                return return_value;
                            }

                            if (bizagi.util.isArray(return_value) && return_value.length > 0) {
                                return return_value; //If the returned value is not undefined, neither null, it will count +1
                            }
                        }, column, columnIndex);

                        break;

                    case "max":
                        result = rows.max(function (row) {
                            var return_value = row[columnIndex] !== null ? row[columnIndex] : column.bizAgiProperties.defaultvalue;
                            return Number(return_value)
                        }, columnIndex);

                        break;

                    case "min":
                        result = rows.min(function (row) {
                            var return_value = row[columnIndex] !== null ? row[columnIndex] : column.bizAgiProperties.defaultvalue;
                            return Number(return_value);
                        }, columnIndex);
                        break;

                    case "avg":
                        result = rows.avg(function (row) {
                            var return_value = row[columnIndex] !== null ? row[columnIndex] : column.bizAgiProperties.defaultvalue;
                            return Number(return_value);
                        }, columnIndex);

                        result = result.toFixed(2);
                        break;
                }

                if (operationState) {
                    result = self.formatTotalizedResult(operationState, column.bizAgiProperties.type, result);
                    showTotal.push({label: column.label, total: result});
                }
            }
        });
        self.showTotals(showTotal);
        bizagi.loading.stop();
    },

    formatTotalizedResult: function (operation, controlType, value) {
        switch (operation) {
            case 'sum':
            case 'avg':
            case 'min':
            case 'max':
                if (controlType == "columnMoney" || controlType == "money") {
                    return bizagi.util.formatMonetaryCell(value);
                }
                break;
        }

        return value || 0;
    },

    showTotals: function (params) {
        var self = this;
        var content = $.fasttmpl(self.renderFactory.getTemplate('bizagi.grid.totalizer'), {items: params});

        self.grid.mobileView({
            displayName: bizagi.localization.getResource("render-grid-totalizer"),
            orientation: self.properties.orientation,
            style: "width: 80%; height: 70%",
            transition: "fade:in",
            cssClass: "bz-modal-view--totalizer",
            showBorder: true,
            contentTemplate: content
        });
    },

    changeBackgroundColor: function (color) {
        var control = this.getControl();
        if (control) {
            if (color !== 'none') {
                $(".bz-tablet-grid", control).css("background-color", color);
            } else {
                $(".bz-tablet-grid", control).css("background-color", "");
            }
        }

        return this;
    },

    /**
     * Get Cell value with format
     * @param renderColumn
     * @param _value
     * @returns {string}
     */
    getCellValueFormatted: function (renderColumn, _value) {
        var dataType = renderColumn.properties.type;
        var formatted = _value;

        if (dataType === "columnDate") {
            var columnProperties = renderColumn.data.properties;

            if (!bizagi.util.isEmpty(formatted)) {
                // Formats the value in full invariant (with time) in order to send to the server
                var date = bizagi.util.dateFormatter.getDateFromInvariant(formatted, columnProperties.showTime);

                if (typeof (date) != "undefined" && date !== 0) {
                    if (!columnProperties.showTime) {
                        date.setHours(0, 0, 0, 0);
                    }

                    formatted = bizagi.util.dateFormatter.formatInvariant(date, true);
                }
            } else if (_value === "") {
                formatted = "";
            }
        }

        return formatted;
    },

    /**
     * Defines the handler when a cell changes its value
     * @param renderColumn
     * @param cell
     * @param key
     * @param args
     */
    onCellChange: function (renderColumn, cell, key, args) {
        var self = this;
        var _value = renderColumn.getValue(key);
        var compositeValue = renderColumn.getCompositeValue(key);
        var dataType = renderColumn.properties.type;

        try {
            if (dataType === "columnDate") {
                if (!bizagi.util.isEmpty(_value)) {
                    var date = bizagi.util.dateFormatter.getDateFromInvariant(_value, true);
                    date.setSeconds(0);
                    date.setMilliseconds(0);

                    _value = bizagi.util.dateFormatter.formatInvariant(date, true);
                } else if (_value === "") {
                    _value = "";
                }
            }

            var isExclusiveControl = dataType === "columnBoolean" && renderColumn.properties.isExclusive;

            if (!isExclusiveControl && (self.properties.allowEdit)) {
                self.collectGridChange($.extend(args, {
                    id: key,
                    xpath: renderColumn.properties.xpath || renderColumn.properties.id,
                    value: _value,
                    compositeValue: compositeValue,
                    columnIndex: renderColumn.columnIndex,
                    trigger: true
                }));
            }

            // Update change in grid's component data
            self.triggerGridPluginHandler("changeCellValue", key, renderColumn.columnIndex, _value);

        } catch (e) {
        }
    },

    /**
     * Draws the editable version for the cell
     * @param renderColumn
     * @param properties
     * @param key
     * @param value
     * @param isNewRow
     */
    drawEditableCell: function (renderColumn, properties, key, value, isNewRow) {
        var self = this;
        var defer = new $.Deferred();

        // Set editable
        var originalEditable = renderColumn.properties.editable;
        renderColumn.properties.editable = true;

        // Fix problem when required property lose
        renderColumn.overrideProperties({required: properties.required});

        // Render cell
        $.when(renderColumn.render(key, value)).done(function (cell) {

            // Register array with new records
            (self.column || []).forEach(function (item, index) {
                var visible = true;

                self.cellOverrides[key] = self.cellOverrides[key] || {};

                if (typeof (self.getCellOverride(key, index).visible) !== "undefined") {
                    visible = self.getCellOverride(key, index).visible;
                }

                self.getCellOverride(key, index).visible = visible;
            });

            // Add change handler
            renderColumn.getDecorated(key).bind("renderchange", function (render, args) {
                self.onCellChange(renderColumn, cell, key, args);
            });

            // Resolve cell
            defer.resolve(cell);
        });

        // Restore editable
        renderColumn.properties.editable = originalEditable;

        // Return promise
        return defer.promise();
    },

    /**
     * Draws the readonly version for the cell
     * @param renderColumn
     * @param properties
     * @param key
     * @param value
     */
    drawReadonlyCell: function (renderColumn, properties, key, value) {
        var self = this;
        var defer = new $.Deferred();

        $.when(renderColumn.renderReadOnly(key, value)).done(function (cell) {
            // Register array with new records
            (self.column || []).forEach(function (item, index) {
                var visible = true;

                self.cellOverrides[key] = self.cellOverrides[key] || {};

                if (typeof (self.getCellOverride(key, index).visible) !== "undefined") {
                    visible = self.getCellOverride(key, index).visible;
                }

                self.getCellOverride(key, index).visible = visible;
            });

            // Resolve cell
            defer.resolve(cell);
        });

        return defer.promise();
    },

    /**
     * Check If xpath or id of column is valid
     * @param columnXpath
     * @param columnId
     * @param xpath
     * @returns {boolean}
     */
    isValidXPathId: function(columnXpath, columnId, xpath){
        return columnXpath == xpath || columnId == xpath || columnXpath + "." + columnId == xpath;
    },

    /**
     * Triggers the grid plugin event
     * @returns {*}
     */
    triggerGridPluginHandler: function () {
        try {
            var args = Array.prototype.slice.call(arguments, 0);
            return this.grid.bizagi_grid_tablet.apply(this.grid, args);
        } catch (e) {
        }
    },
    addExclusiveChange : function(id, data){
        if(this.properties.hasExclusiveCheck){
            for(xpath in data){
                 var column = this.columns.find(function(el){
                     return el.properties.xpath == xpath;
                 });
                 // Check if some of the changes from the edit or add form match with the exclusive controls in the grid
                 var value = data[xpath];
                 if(column && column.properties.isExclusive && value){
                     this.exclusiveChanges[xpath] =  {
                         xpath : xpath,
                         simpleXpath: xpath,
                         value : value,
                         key:id
                     }
                 }
            }                    
        }
    }

});
