/**
 *   Name: Bizagi Tablet Render Grouped Grid Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the Grouped Grid render class to adjust to tablet devices
 */

// Extends from Grid Tablet base
bizagi.rendering.tablet.grid.extend("bizagi.rendering.tablet.grid.grouped", {}, {

    /**
     * Method to customize each render after processed
     */
    postRender: function () {
        this.getControlFiller().addClass("bz-form-item__control--hidden");
    },

    /**
     * Method to customize each render after processed in read-only mode
     */
    postRenderReadOnly: function () {
        this.getControlFiller().addClass("bz-form-item__control--hidden");
    }

});