// Extends from container tablet base
bizagi.rendering.tablet.container.extend("bizagi.rendering.tablet.accordionItem", bizagi.rendering.accordionItem.prototype);
// Extends itself
bizagi.rendering.tablet.accordionItem.extend("bizagi.rendering.tablet.accordionItem", {}, {


    /* 
    *   Change selected item 
    */
    setAsActiveContainer: function (argument) {
        
    },

    /* 
    *   Focus on container
    */
    focus: function () {        
        this.setAsActiveContainer();

        // Call base
        this._super();
    }        
});
